@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
    position: absolute;
    background-color: rgba(0, 0, 0, .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario{
        max-width: 60rem;
        background-color: v.$rosa;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 1000ms;
        opacity: 0;
        border-radius: 1rem;

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
            color: v.$blanco;
        }

        label{
            font-weight: bold;
            color: v.$blanco;
        }
        input[type="text"]{
            border: 1px solid v.$morado;
            border-radius: .5rem;
        }

        .opciones{
            display: flex;
            justify-content: space-between;
        }
        
        .submit-nueva-tarea{
            @include m.boton(v.$morado, v.$blanco);
        }

        .cerrar-modal{
            @include m.boton(v.$rojo, v.$blanco);

        }

        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;

        }

        &.cerrar{
            position: relative;
            top: 50%;
            transform: translateY(-200%);

        }
    }
}