  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #760000;
