@use 'base/variables' as v;
@use 'base/mixins' as m;


.crear{
    margin-top: 5rem;
    .uptask{
      @include m.textDegradado(v.$rojo, v.$naranja);
    }

    input[type="submit"]{
      @include m.boton(v.$rojo, v.$blanco);
    }

    .acciones{
      a{
        color: v.$rojo;
      }
        
    }
}