@use 'base/variables' as v;
@use 'base/mixins' as m;


.olvide{
    margin-top: 5rem;
    .uptask{
      @include m.textDegradado(v.$verde, v.$naranja);
    }

    input[type="submit"]{
      @include m.boton(v.$verde, v.$blanco);
    }

    .acciones{
      a{
        color: v.$verde;
      }
        
    }
}